	data() {
		return {
			isTrue:false,//是否存在登陆态
			personInfo:{},
			isfirst:true,
			headerPosition:"fixed",
			headerTop:null,
			statusTop:null,
			//个人信息,
			user:{
				username:'游客1002',
				face:'../../static/img/face.jpg',
				signature:'',
				integral:0,
				balance:0,
				envelope:0
			},
			// 订单类型
			orderList:[
				{text:'待付款',icon:"fukuan"},
				{text:'待发货',icon:"fahuo"},
				{text:'待收货',icon:"shouhuo"},
				{text:'待评价',icon:"pingjia"},
				{text:'退换货',icon:"tuihuo"}
			],
			// 工具栏列表
			mytoolbarList:[
				{url:'../../userPage/keep/keep',text:'我的收藏',img:'../../static/img/user/point.png'},
				{url:'../../userPage/coupon/coupon',text:'优惠券',img:'../../static/img/user/quan.png'}, 
				// {url:'',text:'新客豪礼',img:'../../static/img/user/renw.png'},
				// {url:'',text:'领红包',img:'../../static/img/user/momey.png'},
				// 
				{url:'../../userPage/address/address',text:'收货地址',img:'../../static/img/user/addr.png'},
				// {url:'',text:'账户安全',img:'../../static/img/user/security.png'},
				// {url:'',text:'银行卡',img:'../../static/img/user/bank.png'},
				// {url:'',text:'抽奖',img:'../../static/img/user/choujiang.png'},
				// {text:'客服',img:'../../static/img/user/kefu.png'},
				// {text:'签到',img:'../../static/img/user/mingxi.png'}
				
			]
		}
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
		this.headerTop = e.scrollTop>=0?null:0;
		this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
	},
	onLoad() {
		this.statusHeight = 0;



	},
	onReady(){
		uni.showToast({
			title: 'loading...',
			icon:'loading',
			image:"../../static/img/user/tre.gif",
			duration:5000
		});
		//此处，演示,每次页面初次渲染都把登录状态重置
		// uni.setStorage({
		// 	key: 'UserInfo',
		// 	data: false,
		// 	success: function () {
		// 	},
		// 	fail:function(e){
		// 	}
		// });
	},
	onShow(){
		uni.getStorage({
			key: 'skey',
			success: (res)=>{ //session存在skey，执行1
				this.toCheckSession();
			},
			fail:(e)=>{
				//登陆.....tongshang
			}
		});
		
	},
	methods: {
		toCheckSession(){
			uni.checkSession({
				success: (res) => {
					this.isTrue = true;
					//直接拿openId
					this.toGetUserData();
				},
				fail: (err) => {//没有登陆态
					// this.toLogin();
					//登陆=》登陆后通过code获取session_key 转换成skey存入storage
				}
			});
		},
		wxGetUserInfo(res){
			if (!res.detail.iv) {
				uni.showToast({
					title: "您取消了授权,登录失败",
					icon: "none"
				});
				return false;
			}
			console.log('-------用户授权，并获取用户基本信息和加密数据------')
			this.personInfo = res.detail.userInfo;
			this.toLogin();

		},
		toOrderList(index){
			uni.navigateTo({url:'../../userPage/order_list/order_list?tbIndex='+index}) 
		},
		toSetting(){
			uni.navigateTo({
				url:'../../userPage/setting/setting'
			})
		},
		toMyQR(){
			uni.navigateTo({
				url:'../../userPage/myQR/myQR'
			})
		},
		toGetUserData(){
			uni.getUserInfo({
				success: (c) => {
					this.personInfo = c.userInfo;
				}
			});
		},
		toLogin(){
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log('-------获取code-------')
					console.log(loginRes.code);
					this.getSkeyAndOpenId(loginRes.code);
					this.isTrue = true;
				}.bind(this)
			});
		},
		getSkeyAndOpenId(code){
			// code换取session_key跟openid  
			// 这一步最好在后台做 也就是我直接传code给后台 后台写死secret跟appid 
			// 然后得到session_key跟openid 用session_key进行sha1/md5加密后 把skey,openid给我
			let secret = '119afffd251e8b1b6e0f0a96e977bb7d';
			let appid = 'wx0904846d9b4c40fe';
			uni.request({
				url: `https://api.weixin.qq.com/sns/jscode2session?appid=${appid}&secret=${secret}&js_code=${code}&grant_type=authorization_code`,//change
				success: function(info) {
					this.saveStorage('skey',info.data.session_key);
					this.saveStorage('openid',info.data.openid);
				}.bind(this),
				fail: function(e) {
					console.log(e)
				}
			});
		},
		saveStorage(name,data){
			uni.setStorage({
				key: name,
				data: data,
				fail: (err) => {
					console.log(err);
				}
			});
		},
		isLogin(){
			//实际应用中,用户登录状态应该用token等方法去维持登录状态.
			const value = uni.getStorageSync('UserInfo');
			if (value) {
				return true;
			}
			return false
		},
		toDeposit(){
			if(this.isTrue){
				uni.navigateTo({
					url:'../../userPage/deposit/deposit'
				});
			}else{
				uni.showToast({
					title:'请先登陆',
					icon:'none'
				});
			}
		},
		toPage(url){
			if(this.isTrue){
				uni.navigateTo({
					url:url
				});
			}else{
				uni.showToast({
					title:'请先登陆',
					icon:'none'
				});
			}
			
		}
	}
} 
